import React, {Component} from 'react'
import "./stylesheets/PortfolioItem.css"


class PortfolioItem extends Component{
    render(){
        return (
            <div className = "portfolioItem">
                    <div className = "link">
                        <a href = {this.props.url} target = "_blank" rel = "norefferer noopener"> Go to Page</a>
                    </div>
                    <div dangerouslySetInnerHTML={{__html:this.props.content?this.props.content:"Got Nothing"}}></div>
                    
            </div>
        )
    }
}



export default PortfolioItem;


