import React, {useState} from 'react'
import "./stylesheets/MainPage.css"


const Scroller = (props) =>{
    let containerRef = React.createRef();
    let [position, setPosition] = useState(0);
    let maxElements = props.children.length;
    function moveLeft(e){ 
        if (position < -maxElements + 2){
            setPosition(0);
            containerRef.current.animate([
                {left: containerRef.current.style.left},
                {left: "0%"}
            ],
            {
                duration: 500,
                iterations: 1
            });
            return;
        }
        containerRef.current.animate([
            {left: `${position * 100}%`},
            {left: `${(position - 1) * 100}%`}
        ],
        {
            duration: 500,
            iterations: 1
        });
        setPosition(position - 1);
    }
    function moveRight(e){ 
        if (position === 0){
            setPosition(-maxElements + 1);
            containerRef.current.animate([
                {left: containerRef.current.style.left},
                {left: `${(-maxElements + 1) * 100}%`}
            ],
            {
                duration: 500,
                iterations: 1
            });
            return;
        }
        containerRef.current.animate([
            {left: `${position * 100}%`},
            {left: `${(position + 1) * 100}%`}
        ],
        {
            duration: 500,
            iterations: 1
        });
        setPosition(position + 1);
    }
    return <>
        <button onClick={moveRight} className = "scrollButton" id = "leftButton">&lt;</button>
        <div className = "portfolioContainer" ref = {containerRef}style = {{left: `${(position) * 100}%`}}>
            {props.children}
        </div>
            <button onClick={moveLeft} className = "scrollButton" id = "rightButton">&gt;</button>
    </>
}

export default Scroller;