import React, {Component, useEffect, useState} from 'react'
import "./stylesheets/MainPage.css"
import {Counter} from "./Testing"
import PortfolioItem from './PortfolioItem.js'
import Scroller from "./scroller"
const {Octokit} = require("@octokit/core")
const showdown = require('showdown')

const GITHUBTOKEN = "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";
const USER_NAME = 'Dev-AviSingh'

const Header = (props) =>{

    return <div className = "header">
        <span>WELCOME TO MY PORTFOLIO</span>
    </div>
}
const Tech = (props) =>{
    return <div className='techBox'>
        <img width={100} height = {100} src = {props.logoUrl} />
        <br/>
        {props.techName}
    </div>
}
const Technologies = (props) =>{
    
    return <div>
        <Tech techName = "Python" logoUrl = "/pythonLogo.png"/>
        <Tech techName = "Flask" logoUrl = "/flaskLogo.webp"/>
        <Tech techName = "ReactJS" logoUrl = "/reactjsLogo.png"/>
        <Tech techName = "Android" logoUrl = "/androidLogo.png"/>
        <Tech techName = "Godot" logoUrl = "/godotLogo.png"/>
    </div>
}

const InfoPage = (props) =>{
    return <div className = "infoPage"> 
        <h1>Who am I?</h1>
        <hr />
        Computer Science student.
        <br />
        I love creating new stuff and solving problems.
        <br /><br />
        <h1>Technologies I know</h1>
        <hr />
        <Technologies/>
    </div>
}

const PageHandler = (props) =>{
    let [currentPage, setCurrentPage] = useState(0)

    let pages = []
    for(let i = 0; i < props.pageCount; i++){
        pages.push(<div key = {props.pageNames[i]} className = {`pageTab${currentPage === i?" highlightedPageTab":""}`} onClick = {() => {setCurrentPage(i); props.pageChangeHandler(i)}}>
            {props.pageNames[i]}
        </div>)
    }
    return <div className = "pageHandler">
        {pages}
    </div>
}



class MainPage extends Component{
    constructor(props){
        super(props)

        this.state = {
            repoDataSet: [],
            currentPage: 0
        }
        this.pageMap = {
            0: "About",
            1: "Github Links"
        }
        this.changePage = this.changePage.bind(this)
    }

    async getContent(repoName) {
        const octokit = new Octokit({auth: GITHUBTOKEN});
        let content;
        try {
            content = await octokit.request(`GET /repos/${USER_NAME}/${repoName}/readme`, {
                owner: USER_NAME,
                repo: repoName
              })
              let decoded = await this.downloadAndDecodeMarkdown(content.data.download_url)
              return decoded
        } catch (err) {
            //console.log("Did not get read me for " + repoName + err)
        }

        
    }

    async downloadAndDecodeMarkdown(fileURL) {

        const response = await fetch(fileURL)
        const blob = await response.blob()
        const data = await blob.text()
        let converter = new showdown.Converter()
        let decodedMarkdown = converter.makeHtml(data)

        return decodedMarkdown

    }

    changePage(index){
        this.setState({currentPage:index})
    }

    render(){
        let currentPage = (<Scroller>
            {Array.from(this.state.repoDataSet.filter(i => i.content), repoData => <PortfolioItem key = {repoData.id} repoData = {repoData} user = {USER_NAME} content = {repoData.content} url = {repoData.url}></PortfolioItem>
            
            )}
        </Scroller>)
        
        switch(this.state.currentPage){
            default:
            case 1:
                break;
            case 0:
                currentPage = (<InfoPage />)
                break;
                
        }

        return  <div className = "mainPage">
            <Header />
            <PageHandler pageCount = {2} pageNames = {["ME", "Github Pages"]} pageChangeHandler = {this.changePage}></PageHandler>
            <div className = "mainBody">
                {currentPage}
            </div>
        </div>
    }

    async getRepos(){
        const octokit = new Octokit({auth: GITHUBTOKEN});

        let response = await octokit.request('GET /user/repos', {type: "public"});
        for(let r of response.data.reverse()){
            try{
                let content = await this.getContent(r.name)
                    this.setState({repoDataSet: [... this.state.repoDataSet, {
                        id: r.id,
                        name: r.name,
                        content: content,
                        url: r.html_url
                    }
                ]
                }
                );
            }catch(err){
                console.log("Error when tried to get repo data for " + r.name + "Error message : " + err);
            }

            
        }
    }
    componentDidMount(){
        this.getRepos();
    }
}


export default MainPage;